body {
  text-align: center;
  background-color: rgb(130, 88, 159);
}
.heading {
  margin: 20px;
}
.tosee {
  border: 2px solid black;
  border-radius: 25px;
  display: inline-block;
  margin: 50px;
  padding: 15px;
  background-color: rgb(202, 211, 200);
}
.tosee:hover {
  background-color: rgb(154, 236, 219);
}

.done {
  border: 2px solid black;
  border-radius: 15px;
  display: inline-block;
  margin: 50px;
  padding: 15px;
  background-color: rgb(202, 211, 200);
}

.App {
  font-family: sans-serif;
  text-align: center;
}
